.professional-result[data-v-ec3b89bc] {
  background-image: url(../../assets/professional/result-bg.jpg);
  background-size: 100% auto ;
  background-repeat: no-repeat;
  height: 100% ;
  padding: 8rem 0 3rem 0 ;
}
.professional-result header[data-v-ec3b89bc] {
  background-image: url(../../assets/professional/caption-bg-result.png);
  background-size: 100% 18rem ;
  background-repeat: no-repeat;
  margin: 0 .8rem ;
  min-height: 20rem ;
}
.professional-result header p[data-v-ec3b89bc] {
  border-bottom: 2px dashed #000 ;
  font-size: 2.2rem ;
  line-height: 2.8rem ;
  margin: 0 1.5rem ;
  padding: 2.5rem 0 1.5rem 0 ;
  text-align: center ;
}
.professional-result header p[data-v-ec3b89bc]:last-child {
  border: none ;
  padding: 1.5rem 0 2.5rem 0 ;
}
.professional-result header p .points[data-v-ec3b89bc] {
  color: #ff3939 ;
}
.professional-result ul[data-v-ec3b89bc] {
  margin: 0 1rem ;
}
.professional-result ul li[data-v-ec3b89bc] {
  border: 1px solid #4e4e4e ;
  border-radius: 12px;
  box-shadow: 0 -5px 1px 1px rgba(0, 0, 0, 0.08) inset;
  background-color: #fff ;
  font-size: 1.7rem ;
  line-height: 2rem ;
  margin: 1rem .5rem 0 .5rem ;
  padding: .5rem .5rem 1rem .5rem ;
  text-align: center ;
}
.professional-result ul li a[data-v-ec3b89bc] {
  color: #000;
}
.professional-result .mask[data-v-ec3b89bc] {
  background-color: rgba(0, 0, 0, 0.35);
  height: 100% ;
  left: 0 ;
  position: fixed ;
  top: 0 ;
  width: 100% ;
}
.professional-result .mask[data-v-ec3b89bc]:after {
  clear: both ;
  content: " ";
  display: block ;
  font-size: 0 ;
  height: 0 ;
  visibility: hidden ;
}
.professional-result .mask .guide[data-v-ec3b89bc] {
  float: right ;
  padding: 0 1rem ;
  width: 70% ;
}



